import React, { useEffect, useState, useContext, useRef } from 'react'
import { ThemeContext } from '../theme-context';

import * as Dom from '../../utils/dom'
import * as Storage from '../../utils/storage'
import { THEME } from '../../constants'

const getTheme = () => Storage.getTheme(Dom.hasClassOfBody(THEME.DARK));

const src = 'https://utteranc.es/client.js'
const branch = 'master'
let utterances = null;

export const Utterences = ({ repo }) => {
  useContext(ThemeContext);
  const isChanged = useRef(null);
  const rootElm = React.createRef()
  useEffect(() => {
    const theme = getTheme();
    if(isChanged.current === theme) {
      return;
    }
    isChanged.current = theme;
    rootElm.current.firstChild && rootElm.current.removeChild(rootElm.current.firstChild);
    utterances = document.createElement('script')
    const utterancesConfig = {
      src,
      repo,
      branch,
      async: true,
      'issue-term': 'pathname',
      crossorigin: 'anonymous',
      theme: theme ? "github-dark" : "github-light"
    }
    Object.keys(utterancesConfig).forEach(configKey => {
      utterances.setAttribute(configKey, utterancesConfig[configKey])
    })
    rootElm.current.appendChild(utterances)
  })

  return <div className="utterences" ref={rootElm} />
}